button,
.button {
  cursor: pointer;
  display: inline-flex;
  padding: 10px 20px;
  border-radius: 2px;
  border-width: 2px;
  border-style: solid;
  border-color: #05d8ff;
  background-color: #05d8ff;
  color: white;
  font-weight: bold;
  font-size: 16px;
  text-decoration: none;
  transition: background-color 0.4s ease-in-out, border-color 0.4s ease-in-out; }
  button:hover, button:focus,
  .button:hover,
  .button:focus {
    background-color: #52e4ff;
    border-color: #52e4ff; }
  button[disabled], button.disabled,
  .button[disabled],
  .button.disabled {
    opacity: 0.7;
    cursor: default;
    color: white;
    background-color: #05d8ff;
    border-color: #05d8ff; }

